@font-face {
  font-family: 'Norse';
  src: url('./fonts/Norse.eot');
  src: url('./fonts/Norse.eot?#iefix') format('embedded-opentype'),
      url('./fonts/Norse.woff2') format('woff2'),
      url('./fonts/Norse.woff') format('woff'),
      url('./fonts/Norse.ttf') format('truetype'),
      url('./fonts/Norse.svg#Norse') format('svg');
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: 'Norse', cursive;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.card-container {
  position: relative;
  top: 0px;
  width: 90%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}



